.sami {
  position: relative;
  height: 100vh;
  width: 100vw;
}
.app-wrapper {
  position: relative;
  overflow: hidden;
}
.bg-shadow {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: 20;
  background-color: #000;
  opacity: 0.8;
}
.App {
  text-align: center;
  background-color: #f6f6f6;
  padding: 0 30px;
  border: 1px solid #dfdfdf;
  border-top: 0;
  border-bottom: 0;
}
.container {
  max-width: 1100px;
  margin: 0 auto;
}
ul {
  padding: 0;
}
ul li {
  list-style-type: none;
}
a {
  text-decoration: none;
}
body {
  font-family: "Manrope", sans-serif;
  overflow-x: hidden;
}
.active,
.selected {
  background-color: #fc153b !important;
  color: #fff !important;
  border-color: #fc153b !important;
  transition: ease 0.4s;
}
.wrapper {
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
  padding: 0 60px;
  background-color: #fff !important;
}
