.ProfileNav-site-nav {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0 0 30px;
}
.nav-shape-bg {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  /* UI Properties */
  background: #ffffff 0% 0% no-repeat padding-box;
  border: 1px solid #dfdfdf;
  border-radius: 30px;
  opacity: 1;
}

.ul-outer {
  position: relative;
  display: flex;
}
.ProfileNav-site-ul ul {
  display: flex;
}
.ProfileNav-site-ul ul li {
  transition: ease 0.2s;
}
.ProfileNav-site-ul ul li button {
  padding: 12px 28px;
  background-color: #fff;
  border-radius: 30px;
  border: 1px solid #dfdfdf;
  /* font-size: 18px; */
  font: normal normal 600 18px Manrope;
  letter-spacing: 0px;
  color: #1d1d1d;
  margin: 5px 5px;
}
.ProfileNav-nav-links-hover:hover,
.ProfileNav-nav-links-hover:focus {
  -webkit-transform: scale(0.9);
  transform: scale(0.9);
  transition: ease 0.2s;
}
.ProfileNav-site-ul-bg-shape {
  background: #fff 0% 0% no-repeat padding-box;
  border: 1px solid #dfdfdf;
  border-radius: 30px;
  border-bottom: 0;
  display: flex;
}
.noRmargin {
  margin-right: 0 !important;
}
.noLmargin {
  margin-left: 0 !important;
}
.ProfileNav-shapeLeft {
  height: 60px;
  display: flex;
  flex: 1;
  border-bottom: 1px solid #dfdfdf;
  border-radius: 30px;
  margin-right: -4px;
}
.ProfileNav-shapeLeftInner {
  width: 60px;
  height: 60px;
  justify-content: start;
  background-color: #fff;
}
.ProfileNav-shapeRight {
  height: 60px;
  border-bottom: 1px solid #dfdfdf;
  border-radius: 30px;
  display: flex;
  flex: 1;
  justify-content: flex-end;
  margin-left: -4px;
}
.ProfileNav-shapeRightInner {
  width: 60px;
  height: 60px;
  justify-content: end;
  background-color: #fff;
}
