.intro {
  padding: 80px 0;
  position: relative;
}
.lotti-player {
  position: absolute;
  top: -5%;
  right: 20%;
}
.intro h1 {
  text-align: center;
  font: normal normal bold 60px/66px Manrope;
  margin: 0 0 15px;
  letter-spacing: 2px;
  color: #1d1d1d;
  opacity: 1;
}
.intro h3 {
  font: normal normal normal 24px/30px Manrope;
  margin: 0;
  letter-spacing: 0px;
  color: #000000;
  opacity: 1;
}
.case-study {
  padding-bottom: 80px;
}
.case-study-ul {
}
.case-study-li {
  display: flex;
  position: relative;
  height: 350px;
  width: 100%;
  border-radius: 30px;
  margin-bottom: 40px;
}
.case-study-li:hover .case-study-img img {
  transform: scale(1.1);
  transition: ease 0.2s;
}
.case-study-li:hover .case-study-img .img-overlay {
  opacity: 0.5;
  transition: ease 0.2s;
}
.case-study-li:hover .case-study-btn {
  opacity: 1;
  transition: ease 0.3s;
}
.case-study-img {
  height: 350px;
  width: 100%;
  border-radius: 30px;
  background-color: #f6f6f6;
  overflow: hidden;
  position: relative;
}
.case-study-img img {
  width: 100%;
  background: 0 0 no-repeat;
  background-size: cover;
  transition: ease 0.2s;
}
.img-overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.1);
  background-image: linear-gradient(
    0deg,
    rgba(0, 0, 0, 0.1) 0,
    transparent 5%,
    rgba(0, 0, 0, 0.1)
  );
  z-index: 10;
  opacity: 0;
  transition: ease 0.2s;
}
.case-study-btn {
  position: absolute;
  left: 50%;
  top: 45%;
  transform: translateX(-50%);
  background: #ff0931 0% 0% no-repeat padding-box;
  border-radius: 12px;
  padding: 17px 24px;
  font-size: 16px;
  color: #fff;
  border: transparent;
  display: flex;
  justify-content: space-between;
  align-items: center;
  opacity: 0;
  z-index: 10;
  transition: ease 0.2s;
  cursor: pointer;
}
.case-study-btn:hover {
  background: #fff 0% 0% no-repeat padding-box;
  color: #ff0931;
  font-weight: 500;
  transition: ease 0.2s;
}
.case-study-btn a {
  display: contents;
  color: #fff;
  text-decoration: none;
  cursor: pointer;
  z-index: 10;
  transition: ease 0.2s;
}
.case-study-btn:hover a {
  color: #ff0931;
  transition: ease 0.2s;
}
.case-study-btn svg {
  margin-left: 8px;
}
