.footer-wrapper {
  padding: 22px 40px;
  border-top: 1px solid #dfdfdf;
  background-color: #fff;
}
.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer h3 {
  font: normal normal 800 18px/22px Manrope;
  letter-spacing: 0px;
  color: #1d1d1d;
  line-height: 1;
}
.contact {
  display: flex;
}
.contact a {
  display: flex;
  align-items: center;
  text-decoration: none;
  border: 1px solid #e1e5f1;
  border-radius: 5px;
  padding: 9px 11px;
  margin-right: 10px;
  cursor: pointer;
}
.contact p {
  font: normal normal medium 14px/19px Manrope;
  letter-spacing: 0px;
  color: #1d1d1d;
  padding-left: 10px;
  line-height: 1;
  margin: 0;
}
