.site-nav {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 30px 0;
}
.nav-shape-bg {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  /* UI Properties */
  background: #ffffff 0% 0% no-repeat padding-box;
  border: 1px solid #dfdfdf;
  border-radius: 30px;
  opacity: 1;
}
.logo {
  transition: ease 0.2s;
}
.logo a {
  display: flex;
}
.logo svg {
  top: 38px;
  left: 448px;
  width: 44px;
  height: 44px;
}
.ul-outer {
  position: relative;
  display: flex;
}
.site-ul ul {
  display: flex;
}
.site-ul ul li {
  transition: ease 0.2s;
}
.site-ul ul li button {
  padding: 12px 28px;
  background-color: #fff;
  border-radius: 30px;
  border: 1px solid #dfdfdf;
  /* font-size: 18px; */
  font: normal normal 600 18px Manrope;
  letter-spacing: 0px;
  color: #1d1d1d;
  margin: 5px 5px;
  outline: none;
}
.nav-links-hover {
  z-index: 10;
}
.nav-links-hover:hover,
.nav-links-hover:focus {
  -webkit-transform: scale(0.9);
  z-index: 10;
  transform: scale(0.9);
  transition: ease 0.2s;
}
.site-ul-bg-shape {
  background: #f6f6f6 0% 0% no-repeat padding-box;
  border: 1px solid #dfdfdf;
  border-radius: 30px;
  border-bottom: 0;
  display: flex;
}
.noRmargin {
  margin-right: 0 !important;
}
.noLmargin {
  margin-left: 0 !important;
}
.shapeLeft {
  height: 60px;
  display: flex;
  flex: 1;
  border-bottom: 1px solid #dfdfdf;
  border-radius: 30px;
  margin-right: -4px;
}
.shapeLeftInner {
  width: 30px;
  justify-content: start;
  background-color: #f6f6f6;
}
.shapeRight {
  height: 60px;
  border-bottom: 1px solid #dfdfdf;
  border-radius: 30px;
  display: flex;
  flex: 1;
  justify-content: flex-end;
  margin-left: -4px;
}
.shapeRightInner {
  width: 30px;
  justify-content: end;
  background-color: #f6f6f6;
}
.hamburger {
  transition: ease 0.4s;
}
.hamburger button {
  background-color: transparent;
  display: flex;
  padding: 15px;
  border: none;
}
.burger {
  position: relative;
  z-index: 10;
}
.burger-ul {
  z-index: 10;
  opacity: 0;
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(4%, 30%);
  width: 220px;
  border-radius: 15px;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}
.burger-li {
  z-index: 10;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 16px 20px;
  font: normal normal bold 14px/22px Manrope;
  letter-spacing: 0px;
  color: #1d1d1d;
}
.burger-li:first-child {
  background: #ffffff 0% 0% no-repeat padding-box;
  border: 1px solid #dfdfdf;
  border-radius: 15px 15px 0px 0px;
}
.burger-li:last-child {
  background: #ffffff 0% 0% no-repeat padding-box;
  border-top: 0;
  border: 1px solid #dfdfdf;
  border-radius: 0px 0px 15px 15px;
}
.burger-li button,
.burger-a {
  text-decoration: none;
  font: normal normal bold 14px/22px Manrope;
  letter-spacing: 0px;
  color: #1d1d1d;
  display: flex;
  flex: 1;
  padding: 0;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}
.burger-a p {
  margin-right: 32px;
}
.opacity1 {
  opacity: 1;
}
