.profileIntro {
  padding: 40px 0;
}
.profile-img-section {
  position: relative;
  margin-bottom: 100px;
}
.profile-cover {
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
  background: #e1e8f5 0% 0% no-repeat padding-box;
  height: 200px;
  width: 100%;
}
.profile-img {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
}
.square-svg {
}
.profile-svg {
  position: absolute;
}
.profile-text {
  position: relative;
}
.profile-text h1 {
  font: normal normal 800 40px/20px Manrope;
  line-height: 1;
  letter-spacing: 0px;
  color: #1d1d1d;
  margin: 10px;
}
.profile-text h3 {
  font: normal normal normal 22px/20px Manrope;
  line-height: 1;
  letter-spacing: 0px;
  color: #1d1d1d;
}
