.popup-wrapper {
  max-width: 320px;
  width: 100%;
  background-color: #fff;
  border-radius: 20px;
  display: none;
  z-index: 30;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  top: 200%;
  transition: all 1s ease-in-out;
}
.showPopup {
  display: block !important;
  position: absolute;
  top: 50% !important;
  transition: all 1s ease-in-out;
}
.popup {
  padding: 26px 20px;
}
.get-in {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 25px;
}
.get-in h3 {
  font: normal normal bold 18px/22px Manrope;
  letter-spacing: 0px;
  color: #1d1d1d;
}
.get-in button {
  background-color: #f4f4f4;
  width: 35px;
  height: 35px;
  border-radius: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  transition: ease 0.2s;
}
.get-in button:hover svg {
  transform: scale(0.9, 0.9);
  transition: ease 0.2s;
}
.get-in button svg {
  z-index: 50;
  transition: ease 0.2s;
}
.phone,
.mail {
  display: flex;
  flex: 1;
  flex-direction: column;
  align-items: baseline;
}
.phone p:first-child,
.mail p:first-child {
  font: normal normal normal 14px/20px Manrope;
  color: #1d1d1d;
  margin-top: 12px;
  margin-bottom: 3px;
}
.phone a,
.mail a {
  display: flex;
  flex-direction: row;
  width: 100%;
  align-items: center;
  padding: 8px 12px;
  border: 1px solid #e1e5f1;
  border-radius: 5px;
}
.phone a p,
.mail a p {
  font: normal normal 600 14px/20px Manrope;
  letter-spacing: 0px;
  color: #1d1d1d;
  margin-left: 10px;
}
