.ProfileItem-wrapper {
  padding: 0;
}
.profileItem-ul {
  max-width: 800px;
  width: 100%;
  padding: 40px 0 60px;
  text-align: center;
}
.profileItem-li {
  border-left: 12px solid #e1e8f5;
  padding: 0 8px 0 40px;
  border-radius: 6px;
  text-align: left;
  margin: 0 0 60px;
}
.job-intro {
  font: normal normal 600 14px/20px Manrope;
  letter-spacing: 0px;
  color: #1d1d1d;
}
.job-type {
  color: #828282;
}
.job-duration {
  color: #828282;
  margin: 5px 0;
}
.job-duration span {
  font: normal normal bold 14px/20px Manrope;
  color: #1d1d1d;
}
.job-title {
  font: normal normal 800 28px/20px Manrope;
  margin: 10px 0;
}
.company-detail {
  display: flex;
  align-items: center;
  flex-direction: row;
  margin: 20px 0;
}
.company-detail img {
  width: 40px;
  height: 40px;
  border-radius: 10px;
  background-color: #ccc;
  margin-right: 15px;
}
.job-description {
  font: normal normal normal 17px/24px Manrope;
}
.about-wrapper {
  padding-bottom: 200px;
  display: flex;
    flex-direction: column;
    align-items: center;
}
.about {
  max-width: 670px!important;
}
.about p {
  font: normal normal normal 20px/29px Manrope;
  letter-spacing: 0px;
  color: #1d1d1d;
}
.social-p {
  font: normal normal bold 24px/29px Manrope;
  letter-spacing: 0px;
  color: #1d1d1d;
  margin: 10px 0 38px;
}
.social {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
}
.social a {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #f6f6f9 0% 0% no-repeat padding-box;
  border: 1px solid #e1e5f1;
  width: 60px;
  height: 60px;
  border-radius: 30px;
  cursor: pointer;
  margin: 0 9px;
}

/* -----------Education Section --------- */
.education-wrapper {
  padding-bottom: 150px;
}
.education-ul {
  position: relative;
}
.education-li:first-child::before {
  content: "";
  border: 2px dashed #dfdfdf;
  display: inline-block;
  height: 40px;
}
.education-li::after {
  content: "";
  border: 2px dashed #dfdfdf;
  display: inline-block;
  height: 40px;
}
.education-ul::after {
  content: "";
  display: inline-block;
  height: 14px;
  width: 14px;
  background: #ee1b24 0% 0% no-repeat padding-box;
  border-radius: 30px;
  position: absolute;
  left: 50%;
  transform: translateX(-7px);
  bottom: 0;
}
.education-li {
  z-index: 1;
}
.education {
  padding: 35px 0;
}
.Institute {
  font: normal normal bold 14px/20px Manrope;
  letter-spacing: 0px;
  color: #1d1d1d;
  margin-bottom: 5px;
}
.degree {
  font: normal normal 800 24px/30px Manrope;
  letter-spacing: 0px;
  color: #1d1d1d;
  margin-bottom: 5px;
}
.duration {
  font: normal normal 600 14px/20px Manrope;
  letter-spacing: 0px;
  color: #828282;
}
